import React from 'react'
import Button from '../button'
import * as S from './style'

const ItemCard = ({ color, title, price, setOpenModal, children }) => (
  <>
    <S.CardTitle color={color}>{title}</S.CardTitle>
    <S.ItemBody color={color}>{children}</S.ItemBody>
    <S.CardFooter>
      <Button onClick={() => setOpenModal(true)}>Get Started</Button>
      <h5>{price}</h5>
    </S.CardFooter>
  </>
)

export default ItemCard
